html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Staatliches", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
  background-color: #FFF;
}

[class*=__titulo] {
  margin: 2rem 0;
}

h1 {
  font-size: 3.6rem;
  font-weight: 700;
}

[class*=__contenedor] {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

.header__title {
  background-image: linear-gradient(to right, #4ab1ff, #0bebfe);
  font-size: 6rem;
  line-height: 1.1;
  margin: 0;
  text-align: center;
  font-weight: 500;
  padding: 2rem 0 3rem;
}

.form {
  margin: 4rem 0 10rem;
}
.form .field {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  border: none;
}
@media (min-width: 768px) {
  .form .field--cuatro {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 3rem;
  }
}
@media (min-width: 768px) {
  .form .field--tres {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 3rem;
  }
}
.form .campo {
  flex: 1;
  display: flex;
  flex-direction: column;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.form .campo label {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
}
.form .campo select {
  padding: 1rem;
  border-color: #d1d1d1;
}

.resultados-contenedor {
  padding: 3rem 0 0;
  min-height: 40rem;
}
.resultados-contenedor h2 {
  font-size: 6rem;
  text-align: center;
  font-weight: 600;
}
.resultados-contenedor .resultado {
  padding: 2rem;
  font-size: 2rem;
  text-align: center;
  line-height: 1.5;
  border-bottom: 1px solid #d1d1d1;
}
.resultados-contenedor .resultado:last-of-type {
  border: none;
}
.resultados-contenedor .resultados__alert {
  background-color: #f00;
  padding: 2rem 0;
  text-align: center;
  font-size: 4rem;
  color: #FFF;
  border-radius: 1rem;
  margin: 0;
}
.resultados-contenedor .activo {
  display: block;
}
.resultados-contenedor .inactivo {
  display: none;
}

footer {
  background-image: linear-gradient(to right, #4ab1ff, #0bebfe);
}
footer p {
  font-size: 6rem;
  line-height: 1.1;
  margin: 0;
  text-align: center;
  font-weight: 500;
  padding: 3rem 0 2rem;
  color: #FFF;
}